  function getAttrs (obj, recursion) {
    let attrs = {}
    for (let x in obj) {
      if (obj[x]) {
        let c = obj[x]
        let type = typeof c
        attrs[x] = undefined
        if (type === 'string' && c.length > 1) {
          attrs[x] = c
        } else if (type === 'object') {
          if (!recursion) {
            attrs[x] = getAttrs(c, true)
          }
        }
      }
    }
    return attrs
  }

  let deviceData
