        margin: 0 auto;
        background: #e5e5e5;
        border: 4px solid #e5e5e5;
        overflow: hidden;
        cursor: pointer;

        img {
          width: 100%;
          height: 100%;
        }
      }

      .personal {
        padding: 15px 40px 0;
        text-align: left;
        color: #fff;

        a {
          text-decoration: none;
          color: #fff;
        }

        .icon {
          font-family: wbd, serif;
          margin-right: 8px;
        }
        .text {
        }

        //大标题
        .title {
          .icon {
            float: left;
            display: inline-block;
            width: 30px;
            height: 30px;
            line-height: 32px;
            margin-right: 8px;
            border-radius: 50%;
            background: #fff;
            color: #254665;
            text-align: center;
          }
          .text {
            display: inline-block;
            position: relative;
            top: 3px;
            font-size: 18px;
            font-weight: bold;
            overflow: hidden;
            word-break: break-all;
          }
        }

        //技能
        .skill {
          p {
            margin: 3px 0;
          }

          //进度条
          .slider {
            display: inline-block;
            width: 125px;
            height: 10px;
            background: #f4f4f4;

            //百分比
            .percent {
              display: block;
              width: 100%;
              height: 100%;
              background: #4487c6;
            }
          }
        }

        .interest {
          margin: 5px 0;
        }

      }

    }

    .part-right {
      float: right;
      width: 75%;
      padding-left: 20px;

      .name {
        display: inline-block;
        height: 30px;
        line-height: 29px;
        padding: 10px 30px 0;
        font-size: 30px;
        color: #414141;
      }

      .remark {
        position: relative;
        left: -16px;
        font-size: 10px;
        color: #808080;
      }

      .intro {
        padding: 8px 30px 8px;
        .title {

          position: relative;
          left: -8px;
          margin-top: 10px;
          margin-bottom: 10px;

          .icon {
            display: inline-block;
            width: 40px;
            height: 40px;
            font-size: 24px;
            line-height: 40px;
            margin-right: 10px;
            color: #fff;
            background: #254665;
            border-radius: 50%;
            font-family: wbd, serif;
            text-align: center;
          }
          .text {
            display: inline-block;
            position: relative;
            top: 8px;
            width: 580px;
            height: 30px;
            padding-bottom: 4px;
            font-size: 24px;
            font-weight: bold;
            word-break: break-all;
            overflow: hidden;
            border-bottom: 1px solid #254665;
          }
        }
        .content {
          width: 620px;
          margin-left: 5px;
          line-height: 30px;
          color: #4b4b4b;
          white-space: pre-wrap;
          word-break: break-all;

          a {
            color: #2196f3;
            font-weight: bold;
            text-decoration: none;
          }

          i {
            font-style: normal;
          }

          h2 {
            font-size: 22px;
            margin: 24px 0 16px 0;
            color: #e91e63;
            border-bottom: 1px solid #ccc;
          }
          h2:before {
            content: '>> ';
          }
          h3 {
            display: table;
            width: auto;
            color: #2e7bff;
            font-size: 16px;
            font-weight: bold;
            border-bottom: 1px solid #2e7bff;
          }

          span {
            position: relative;
            top: -2px;
            left: 2px;
            font-size: 14px;
            color: #4487c6;
          }

          img, video {
            width: 100%;
            margin: 5px 0;
            border-radius: 5px;
            box-shadow: rgba(0, 0, 0, 0.1) 0 0 20px 6px;
          }

          img[svg] {
            width: auto;
            position: relative;
            top: 10px;
          }

          img[v] {
            position: relative;
            left: 25%;
            width: 50%;
          }

          img[vv] {
            display: inline-block;
            position: relative;
            margin: 0 2.5%;
            width: 45%;
            height: 450px;
          }
        }

        .icon {
          font-family: wbd, serif;
        }
      }
    }

  }
</style>



